import React from 'react'
import { Route, Routes } from 'react-router'
import Nav from './components/Nav/Nav'
import CcsAddPage from './components/Pages/ccs/CcsAddPage'
import CcConsulAddPage from './components/Pages/consultavel/CcConsulAddPage'
import GiftAddPage from './components/Pages/gifts/GiftAddPage'
import LaraAddPage from './components/Pages/laras/LaraAddPage'
import LoginAddPage from './components/Pages/logins/LoginAddPage'
import Styles from "./MainRyca.module.css"

const MainRyca = () => {
    React.useEffect(() => {
        let token = localStorage.getItem("token")
        if(!token) return window.location.pathname = "/login"
    }, [])

    return (
        <div className={Styles.container}>
            <Nav />
            <div></div>
            <div>
                <Routes>
                    <Route path="CCs" element={<CcsAddPage />} />
                    <Route path="Consultavel" element={<CcConsulAddPage />} />
                    <Route path="Gifts" element={<GiftAddPage />} />
                    <Route path="Laras" element={<LaraAddPage />} />
                    <Route path="Logins" element={<LoginAddPage />} />
                </Routes>
            </div>
        </div>
    )
}

export default MainRyca
