import React from 'react'
import Styles from "./CcConsulAddPage.module.css"
import {GlobalContext} from "../../../.././../../GlobalStoarage"

const CcConsulAddPage = () => {
    const [ccType, setCcType] = React.useState('ITAU')
    const [vireMode] = React.useState(false)
    const [valor, setValor] = React.useState(10)

    const [array, setArray] = React.useState([])
    const [text, setText] = React.useState('')

    const { URL, CONFIG_MODS } = React.useContext(GlobalContext)

    const d = new Date()
    const data = `${d.getDay().toString().padStart(2, "0")}/${d.getMonth().toString().padStart(2, "0")}/${d.getFullYear()} ${d.getHours().toString().padStart(2, "0")}:${d.getMinutes().toString().padStart(2, "0")}`

    function handleChangeGiftType(e) {
        setCcType(e.target.innerText)
    }

    function handleSubmit(e) {
        let arr = text.split("\n");
        let arrToSend = [];
        if(arr.length === 0) return alert("Campo vazio")
        let ccTypeOk = ccType.toLowerCase();

        console.log(URL + `/mods/addCcConsul/${ccTypeOk}`);

        arr.forEach(val => {
            let obj;
            if (!val) return

            let [value, numero, mes, ano, cvv, senha, cpf, nome, limite] = val.split("|")
            
            obj = {
                value: Number(value),
                bin: numero.slice(0, 6),
                valid: `${mes}/${ano}`,
                numero,
                cvv,
                loja: ccTypeOk,
                senha,
                cpf,
                nome,
                limite
            }

            console.log(obj);

            arrToSend.push(obj)
        })

        fetch(URL + `/mods/addCcConsul/${ccTypeOk}`, {
            ...CONFIG_MODS,
            body: JSON.stringify({
                array: arrToSend,
                data
            })
        })
            .then(res => res.json())
            .then(res => alert(res.msg))

        setText('')
    }

    return (
        <div>
            <div className={Styles.title}>CCs Consultaveis</div>
            <div className={Styles.choseGift}>
                <div className={`${ccType === "ITAU" && Styles.active}`} onClick={handleChangeGiftType}>ITAU</div>
                <div className={`${ccType === "MAGAZINE" && Styles.active}`} onClick={handleChangeGiftType}>MAGAZINE</div>
                <div className={`${ccType === "HIPERCARD" && Styles.active}`} onClick={handleChangeGiftType}>HIPERCARD</div>
                <div className={`${ccType === "CREDICARD" && Styles.active}`} onClick={handleChangeGiftType}>CREDICARD</div>
                <div className={`${ccType === "SANTANDER" && Styles.active}`} onClick={handleChangeGiftType}>SANTANDER</div>
            </div>

            {!vireMode ?
                <div className={Styles.addContainer}>
                    <div className={Styles.exemple}>
                        <div className={Styles.titleEx}>Como usar: </div>
                        <div>
                            VALOR|NUMERO|MES|ANO|CVV|SENHA|CPF|NOME|LIMITE
                        </div>
                        <div className={Styles.titleEx}>exemplo: </div>
                        <div>
                            5|555544443332222|12|2028|300|1234|12312312389|Joao da silva|700
                        </div>
                    </div>
                    <div>
                        <textarea
                            type="text"
                            onChange={(e) => setText(e.target.value)}
                            value={text}
                            className={Styles.input} />
                    </div>
                    <div className={Styles.itens}>
                        <button
                            className={Styles.btn}
                            onClick={handleSubmit}
                        >Adicionar</button>
                    </div>
                </div>
                :
                <div></div> 
            }
        </div> 
    )
}

export default CcConsulAddPage
