import React from 'react'
import { GlobalContext } from '../../../GlobalStoarage'
import Button from './components/Button'
import Input from './components/Input'
import Styles from "./Login.module.css"

const Login = () => {
    React.useEffect(() => {
        let token = localStorage.getItem('token')
        if(token) window.location.pathname = "/" 
    }, [])

    const [password, setPassword] = React.useState('')
    const [error, setError] = React.useState(false)
    const Global = React.useContext(GlobalContext)

    const HandleLogin = async () => {
        setError(false)
        const response = await (await fetch(Global.URL + "/auth/login", {
            method: "POST",
            headers:
                { "content-type": "application/json" },
            body: JSON.stringify({ pass: password })
        })).json();
        if (response.error) setError(response.message)
        else {
            localStorage.setItem("token", response.token);
            window.location.pathname = '/'
        }
    }
    
    return (
        <main className={Styles.container}>
            <div className={Styles.content}>
                {error && <div className={Styles.err}>{error}</div>}
                <Input name="Senha:" value={password} setValue={setPassword} />
                <Button name="Logar" onClick={HandleLogin} />
            </div>
        </main>
    )
}

export default Login
