import React from 'react'
import Styles from "./LoginAddPage.module.css"
import {GlobalContext} from "../../../.././../../GlobalStoarage"

const LoginAddPage = () => {
    const [vireMode] = React.useState(false)
    const [text, setText] = React.useState('')
    const [loja, setLoja] = React.useState('')

    const { URL, CONFIG_MODS } = React.useContext(GlobalContext)

    const d = new Date()
    const data = `${d.getDay().toString().padStart(2, "0")}/${d.getMonth().toString().padStart(2, "0")}/${d.getFullYear()} ${d.getHours().toString().padStart(2, "0")}:${d.getMinutes().toString().padStart(2, "0")}`

    function handleSubmit(e) {
        let arr = text.split("\n");
        let arrToSend = [];
        if(arr.length === 0) return alert("Campo vazio")

    
        arr.forEach(val => {
            let obj;
            if (!val) return

            let [mail,senha,Nome,CPF,estado] = val.split("|")
            
            obj = {
                mail,
                senha,
                Nome,
                CPF,
                estado,
            }

            arrToSend.push(obj)
        })

        fetch(URL + `/mods/addLogin`, {
            ...CONFIG_MODS,
            body: JSON.stringify({
                loja,
                array: arrToSend,
                data
            })
        })
            .then(res => res.json())
            .then(res => alert(res.msg))

        setText('')
    }

    return (
        <div>
            <div className={Styles.title}>Logins</div>
            <div className={Styles.select}>
                <select onChange={(e) => setLoja(e.target.selectedOptions[0].innerText)}>
                    <option value="PagSeguro">PagSeguro</option>
                    <option value="MagazineLuiza">MagazineLuiza</option>
                    <option value="ShopTime">ShopTime</option>
                    <option value="Centauro">Centauro</option>
                    <option value="Netshoes">Netshoes</option>
                    <option value="FastShop">FastShop</option>
                    <option value="KABUM">KABUM</option>
                    <option value="Gearbest">Gearbest</option>
                    <option value="Ingresso.com">Ingresso.com</option>
                    <option value="Colombo">Colombo</option>
                    <option value="eFacil">eFacil</option>
                    <option value="Privalia">Privalia</option>
                    <option value="Loja Do Mecanico">Loja Do Mecanico</option>
                    <option value="wish">wish</option>
                    <option value="BelezanaWeb">BelezanaWeb</option>
                    <option value="Submarino">Submarino</option>
                    <option value="Dafiti">Dafiti</option>
                    <option value="Amaro">Amaro</option>
                    <option value={`C&A`}>{"C&A"}</option>
                    <option value="Mariza">Mariza</option>
                    <option value="Vova">Vova</option>
                    <option value="Drogasil">Drogasil</option>
                    <option value="Ultrafarma">Ultrafarma</option>
                    <option value="Drogaraia">Drogaraia</option>
                    <option value="Elo7">Elo7</option>
                    <option value="Riachuelo">Riachuelo</option>
                    <option value="Passarela">Passarela</option>
                    <option value="Novo Mundo">Novo Mundo</option>
                    <option value="Pão de Açúcar">Pão de Açúcar</option>
                    <option value="MegaMamute">MegaMamute</option>
                    <option value="Natura">Natura</option>
                    <option value="WebMotors">WebMotors</option>
                </select>
            </div>
            
            {!vireMode ?
                <div className={Styles.addContainer}>
                    <div className={Styles.exemple}>
                        <div className={Styles.titleEx}>Como usar: </div>
                        <div>
                            EMAIL|SENHA|NOME|CPF|ESTADO
                        </div>
                        <div className={Styles.titleEx}>exemplo: </div>
                        <div>
                            joaosilva@gmail.com|joao321|João da silva|12312312398|SÃO PAULO
                        </div>
                    </div>
                    <div>
                        <textarea
                            type="text"
                            onChange={(e) => setText(e.target.value)}
                            value={text}
                            className={Styles.input} />
                    </div>
                    <div className={Styles.itens}>
                        <button
                            className={Styles.btn}
                            onClick={handleSubmit}
                        >Adicionar</button>
                    </div>
                </div>
                :
                <div></div> 
            }
        </div> 
    )
}

export default LoginAddPage
