import React from 'react'
import Colecao from './components/Colecao'
import Styles from "./Nav.module.css"

const Nav = () => {
    const [active, setActive] = React.useState('') 

    const HandleLogout = () => {
      localStorage.removeItem("token");
      window.location.pathname = "/login"  
    }

    return (
        <div className={Styles.container}>
            <div className={Styles.title}>Ryca Store</div>
            <Colecao setActive={setActive} active={active} name="CCs"/>
            <Colecao setActive={setActive} active={active} name="Consultavel"/>
            <Colecao setActive={setActive} active={active} name="Gifts"/>
            <Colecao setActive={setActive} active={active} name="Laras"/>
            <Colecao setActive={setActive} active={active} name="Logins"/>
            <div className={Styles.btns}>
                <div onClick={HandleLogout}>Sair</div>
            </div>
        </div>
    )
}

export default Nav
