import React from 'react'
import Styles from "./CcsAddPage.module.css"
import {GlobalContext} from "../../../.././../../GlobalStoarage"

const CcsAddPage = () => {
    const [ccType, setCcType] = React.useState('CARD')
    const [vireMode] = React.useState(false)
    const [valor, setValor] = React.useState(10)

    const [array, setArray] = React.useState([])
    const [text, setText] = React.useState('')

    const { URL, CONFIG_MODS } = React.useContext(GlobalContext)

    const d = new Date()
    const data = `${d.getDay().toString().padStart(2, "0")}/${d.getMonth().toString().padStart(2, "0")}/${d.getFullYear()} ${d.getHours().toString().padStart(2, "0")}:${d.getMinutes().toString().padStart(2, "0")}`

    function handleChangeGiftType(e) {
        setCcType(e.target.innerText)
    }

    function handleSubmit(e) {
        let arr = text.split("\n");
        let arrToSend = [];
        if(arr.length === 0) return alert("Campo vazio")
        let giftTp;

        if (ccType === "CARD") giftTp = 'cc';
        if (ccType === "NOME") giftTp = 'ccname';
        if (ccType === "FULL") giftTp = 'ccfull';
        if (ccType === "CONSULTADA") giftTp = 'ccconsul';
        if (ccType === "BONUS") giftTp = 'bonus';

        arr.forEach(val => {
            let obj;
            if(!val) return 
            if (ccType === "CONSULTADA") {
                let [value, numero, mes, ano, cvv, nome, cpf, limite, band, pais, tipo, nivel, emissor] = val.split("|")
                obj = {
                    value: +value,
                    numero: +numero,
                    valid: `${mes}/${ano}`,
                    nome,
                    vender: "RYCA STORE",
                    cvv,
                    bin: numero.slice(0, 6),
                    cpf,
                    limite,
                    band,
                    pais,
                    tipo,
                    nivel,
                    emissor
                }
            }
            if (ccType === "CARD") {
                let [value, numero, mes, ano, cvv, band, pais, tipo, nivel, emissor] = val.split("|")
                console.log(numero);
                obj = {
                    value: +value,
                    numero: +numero,
                    valid: `${mes}/${ano}`,
                    vender: "RYCA STORE",
                    cvv,
                    bin: numero.slice(0, 6),
                    band,
                    pais,
                    tipo,
                    nivel,
                    emissor
                }
            }
            if (ccType === "BONUS") {
                let [value, numero, mes, ano, cvv, band, pais, type, nivel, emissor] = val.split("|")
                obj = {
                    value: +value,
                    numero: +numero,
                    vender: "RYCA STORE",
                    valid: `${mes}/${ano}`,
                    cvv,
                    bin: numero.slice(0, 6),
                    band,
                    pais,
                    type,
                    nivel,
                    emissor
                }
            }
            if (ccType === "NOME") {
                let [value, numero, mes, ano, cvv, nome, band, pais, tipo, nivel, emissor] = val.split("|")
                obj = {
                    value: +value,
                    numero: +numero,
                    valid: `${mes}/${ano}`,
                    vender: "RYCA STORE",
                    cvv,
                    bin: numero.slice(0, 6),
                    nome,
                    band,
                    pais,
                    tipo,
                    nivel,
                    emissor
                }
            }
            if (ccType === "FULL") {
                let [value, numero, mes, ano, cvv, nome, cpf, band, pais, tipo, nivel, emissor] = val.split("|")
                obj = {
                    value: +value,
                    numero: +numero,
                    vender: "RYCA STORE",
                    valid: `${mes}/${ano}`,
                    cvv,
                    bin: numero.slice(0, 6),
                    nome,
                    band,
                    pais,
                    tipo,
                    nivel,
                    emissor,
                    cpf
                }
            }

            arrToSend.push(obj)
        })

        fetch(URL + `/mods/addCc/${giftTp}`, {
            ...CONFIG_MODS,
            body: JSON.stringify({
                array: arrToSend,
                data
            })
        })
            .then(res => res.json())
            .then(res => alert(res.msg))

        setText('')
    }

    return (
        <div>
            <div className={Styles.title}>CCs</div>
            <div className={Styles.choseGift}>
                <div className={`${ccType === "CARD" && Styles.active}`} onClick={handleChangeGiftType}>CARD</div>
                <div className={`${ccType === "NOME" && Styles.active}`} onClick={handleChangeGiftType}>NOME</div>
                <div className={`${ccType === "FULL" && Styles.active}`} onClick={handleChangeGiftType}>FULL</div>
                <div className={`${ccType === "CONSULTADA" && Styles.active}`} onClick={handleChangeGiftType}>CONSULTADA</div>
                <div className={`${ccType === "BONUS" && Styles.active}`} onClick={handleChangeGiftType}>BONUS</div>
            </div>

            {!vireMode ?
                <div className={Styles.addContainer}>
                    <div className={Styles.exemple}>
                        <div className={Styles.titleEx}>Como usar: </div>
                        <div>
                            {ccType === "CARD" && "VALOR|NUMERO|MES|ANO|CVV|BANDEIRA|PAIS|TIPO|NIVEL|EMISSOR"}
                            {ccType === "NOME" && "VALOR|NUMERO|MES|ANO|CVV|NOME|BANDEIRA|PAIS|TIPO|NIVEL|EMISSOR"}
                            {ccType === "FULL" && "VALOR|NUMERO|MES|ANO|CVV|NOME|CPF|BANDEIRA|PAIS|TIPO|NIVEL|EMISSOR"}
                            {ccType === "CONSULTADA" && "VALOR|NUMERO|MES|ANO|CVV|NOME|CPF|LIMITE|BANDEIRA|PAIS|TIPO|NIVEL|EMISSOR"}
                            {ccType === "BONUS" && "PONTOS|NUMERO|MES|ANO|CVV|BANDEIRA|PAIS|TIPO|NIVEL|EMISSOR"}
                        </div>
                        <div className={Styles.titleEx}>exemplo: </div>
                        <div>
                            {ccType === "CARD" && "5|555544443332222|12|2028|300|Master Card|Brasil|Credit|Standard|Itau"}
                            {ccType === "NOME" && "5|555544443332222|12|2028|300|Joao algusto|Master Card|Brasil|Credit|Standard|Itau"}
                            {ccType === "FULL" && "5|555544443332222|12|2028|300|Joao algusto|12345678921|Master Card|Brasil|Credit|Standard|Itau"}
                            {ccType === "CONSULTADA" && "5|555544443332222|12|2028|300|Joao algusto|12345678921|800|Master Card|Brasil|Credit|Standard|Itau"}
                            {ccType === "BONUS" && "PONTOS|555544443332222|12|2028|300|Master Card|Brasil|Credit|Standard|Itau"}
                        </div>
                    </div>
                    <div>
                        <textarea
                            type="text"
                            onChange={(e) => setText(e.target.value)}
                            value={text}
                            className={Styles.input} />
                    </div>
                    <div className={Styles.itens}>
                        <button
                            className={Styles.btn}
                            onClick={handleSubmit}
                        >Adicionar</button>
                    </div>
                </div>
                :
                <div></div> 
            }
        </div> 
    )
}

export default CcsAddPage
