import React from 'react'
import Styles from "./giftAddPage.module.css"
import {GlobalContext} from "../../../.././../../GlobalStoarage"

const GiftAddPage = () => {
    const [giftType, setGiftType] = React.useState('Ifood')
    const [vireMode] = React.useState(false)
    const [valor, setValor] = React.useState(10)

    const [array, setArray] = React.useState([])
    const [text, setText] = React.useState('')

    const { URL, CONFIG_MODS } = React.useContext(GlobalContext)

    const d = new Date()
    const data = `${d.getDay().toString().padStart(2, "0")}/${d.getMonth().toString().padStart(2, "0")}/${d.getFullYear()} ${d.getHours().toString().padStart(2, "0")}:${d.getMinutes().toString().padStart(2, "0")}`

    function handleChangeGiftType(e) {
        setGiftType(e.target.innerText)
    }

    function handleSubmit(e) {
        setArray(text.split("\n"))
        if(array.length === 0) return alert("Campo vazio")
        let giftTp = giftType.replace(' ', "").toLowerCase()
        fetch(URL + `/mods/addGifts/${giftTp}`, {
            ...CONFIG_MODS,
            body: JSON.stringify({
                obj: {
                    keys: array,
                    value: valor
                },
                data
            })
        })
            .then(res => alert(res.statusTex))
    }

    return (
        <div>
            <div className={Styles.title}>Gifts</div>
            <div className={Styles.choseGift}>
                <div className={`${giftType === "Ifood" && Styles.active}`} onClick={handleChangeGiftType}>Ifood</div>
                <div className={`${giftType === "Uber Eats" && Styles.active}`} onClick={handleChangeGiftType}>Uber Eats</div>
                <div className={`${giftType === "Uber" && Styles.active}`} onClick={handleChangeGiftType}>Uber</div>
                <div className={`${giftType === "Netflix" && Styles.active}`} onClick={handleChangeGiftType}>Netflix</div>
                <div className={`${giftType === "Google Play" && Styles.active}`} onClick={handleChangeGiftType}>Google Play</div>
            </div>

            {!vireMode ?
                <div className={Styles.addContainer}>
                    <div className={Styles.valor}>
                        <div>Valor:</div>
                        <input value={valor} onChange={(e) => setValor(e.target.value)} type="number" />
                    </div>
                    <div>
                        <textarea
                            type="text"
                            onChange={(e) => setText(e.target.value)}
                            value={text}
                            className={Styles.input} />
                    </div>
                    <div className={Styles.itens}>
                        <button
                            className={Styles.btn}
                            onClick={handleSubmit}
                        >Adicionar</button>
                    </div>
                </div>
                :
                <div></div> 
            }
        </div> 
    )
}

export default GiftAddPage
