import React from 'react'
import Styles from "./LaraAddPage.module.css"
import {GlobalContext} from "../../../.././../../GlobalStoarage"

const LaraAddPage = () => {
    const [vireMode] = React.useState(false)
    const [text, setText] = React.useState('')

    const { URL, CONFIG_MODS } = React.useContext(GlobalContext)

    const d = new Date()
    const data = `${d.getDay().toString().padStart(2, "0")}/${d.getMonth().toString().padStart(2, "0")}/${d.getFullYear()} ${d.getHours().toString().padStart(2, "0")}:${d.getMinutes().toString().padStart(2, "0")}`

    function handleSubmit(e) {
        let arr = text.split("\n");
        let arrToSend = [];
        if(arr.length === 0) return alert("Campo vazio")

        arr.forEach(val => {
            let obj;
            if (!val) return

            let [value,email,senha,nome,cpf,pix,banco,titular,nivel] = val.split("|")
            
            obj = {
                value: Number(value),
                email,
                senha,
                nome,
                cpf,
                pix,
                banco,
                titular,
                nivel,
                vendedor: "Ryca Store"
            }

            arrToSend.push(obj)
        })

        fetch(URL + `/mods/addLara`, {
            ...CONFIG_MODS,
            body: JSON.stringify({
                array: arrToSend,
                data
            })
        })
            .then(res => res.json())
            .then(res => alert(res.msg))

        setText('')
    }

    return (
        <div>
            <div className={Styles.title}>Laras</div>
            <div className={Styles.choseGift}>
            </div>

            {!vireMode ?
                <div className={Styles.addContainer}>
                    <div className={Styles.exemple}>
                        <div className={Styles.titleEx}>Como usar: </div>
                        <div>
                            VALOR|EMAIL|SENHA|NOME|CPF|PIX|BANCO|TITULAR|NIVEL
                        </div>
                        <div className={Styles.titleEx}>exemplo: </div>
                        <div>
                            30|dasvbdysavd@gmail.com|dasdsa312|João da silva|12312312398|12312312398|Inter|FEMININO|1
                        </div>
                    </div>
                    <div>
                        <textarea
                            type="text"
                            onChange={(e) => setText(e.target.value)}
                            value={text}
                            className={Styles.input} />
                    </div>
                    <div className={Styles.itens}>
                        <button
                            className={Styles.btn}
                            onClick={handleSubmit}
                        >Adicionar</button>
                    </div>
                </div>
                :
                <div></div> 
            }
        </div> 
    )
}

export default LaraAddPage
